header{
    height:100vh;
    padding-top:4rem;
    overflow: hidden;
}
.header-container{
    text-align: center;
    height:100%;
    position:relative;
}
/* ---CTA--- */
.cta{
    margin-top:2.5rem;
    display: flex;
    gap:1rem;
    justify-content: center;
}
/* ---HeaderSocials--- */
.header__social{
    display: flex;
    flex-direction: column;
    align-items:center;
    gap:0.8rem;
    position:absolute;
    left:0;
    bottom:3rem;
}
.header__social::after{
    content:'';
    width:1px;
    height:2rem;
    background: var(--color-primary);
}
/*----Image----*/
.me{
    background: linear-gradient(var(--color-primary),transparent);
    width:22rem;
    height:30rem;
    position:absolute;
    left:calc(50% - 11rem);
    margin-top:3rem;
    border-radius:12rem 12rem 0 ;
    overflow: hidden;
    padding:5rem 1.5rem 1.5rem 1.5rem;
}
/* ----scroll down---- */
.scroll_down{
    position: absolute;
    right:-2.3rem;
    bottom:5rem;
    transform: rotate(90deg);
    font-weight: 300;
    font-size:0.9rem;
}
/* ---Medium Devices Responsive--- */
@media  screen and (max-width:1024px) {
    header{
        height:68vh;
    }
    
}
/* ---Small Devices Responsive--- */
@media  screen and (max-width:600px) {
    header{
        height:100vh;
    }
    .header__social, .scroll_down{
        display: none;
        
    }

    
}